.App {
  text-align: center;
  color: #fedcba; }

table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 15px;
  line-height: 25px; }

th {
  background-color: #eee; }

td,
th {
  text-align: center; }

td:first-child {
  text-align: left; }

input {
  min-width: 300px;
  border: 1px solid #999;
  border-radius: 2px;
  line-height: 25px; }

#main {
  display: flex;
  justify-content: flex-end;
  /* .slide {
    margin-left: 30px;
  } */
  /*  .main-container {
    background: #f8f8f8;
  } */ }
  #main .placeholder {
    width: auto;
    flex: 1; }
  #main #merge {
    width: 100%; }
  #main .box {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-top: 10px; }
    #main .box .btns {
      display: flex;
      justify-content: flex-start;
      align-items: center; }
    #main .box .btn {
      margin-left: 10px; }
    #main .box div {
      margin-left: 10px; }
  #main .example {
    width: 440px;
    min-height: 300px;
    background: #ffffff;
    margin-right: 10px; }
    #main .example .example-header {
      position: relative;
      padding: 16px 24px;
      color: rgba(0, 0, 0, 0.65);
      border-bottom: 1px solid #e8e8e8;
      border-radius: 4px 4px 0 0; }
      #main .example .example-header .example-header-h3 {
        margin: 0;
        color: rgba(0, 0, 0, 0.85);
        font-weight: 500;
        font-size: 16px;
        line-height: 22px; }
    #main .example .ul {
      display: flex;
      justify-content: space-around;
      flex-wrap: wrap; }
    #main .example .li {
      width: 200px;
      height: 306px;
      margin-top: 10px;
      cursor: pointer;
      border: 1px solid #f0f0f0; }
      #main .example .li img {
        width: 100%;
        height: 100%; }

.option {
  margin-top: 10px; }
  .option .option-li {
    margin-top: 10px;
    border: 1px solid #eee;
    padding: 0 20px 10px;
    margin-left: 10px; }
  .option .row {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-top: 10px; }
    .option .row .h3 {
      color: #333;
      font-size: 16px;
      width: 110px;
      margin-right: 10px; }
    .option .row .ant-btn {
      margin-right: 10px; }
    .option .row .ant-input {
      width: 200px;
      min-width: auto; }

.option-drawer {
  /* .option-drawer-header {
    height: 33px;
  } */ }
  .option-drawer .option-li {
    border: 0;
    padding: 0; }
    .option-drawer .option-li .row:first-child {
      margin-bottom: 30px; }
